import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ValuesAddComponent } from './sites/values-add/values-add.component';
import { Rout_ValuesAdd } from './sites/values-add/app-rout-valuesadd';
import { M365Component } from './sites/m365/m365.component';
import { Rout_M365 } from './sites/m365/app-rout-m365';
import { PageWorkingComponent } from './page-working/page-working.component';
import { CostComponent } from './sites/cost/cost.component';
import { MaslGuard } from './guards/masl.guard';

const routes: Routes = [
  { path: 'home', component: HomeComponent, canActivate: [MaslGuard]},
  { path: 'page-working', component: PageWorkingComponent, canActivate: [MaslGuard] },
  { path: 'cost', component: CostComponent, canActivate: [MaslGuard] },
  { path: 'm365', component: M365Component, children: Rout_M365, canActivate: [MaslGuard]},
  { path: 'values-add', component: ValuesAddComponent, children: Rout_ValuesAdd, canActivate: [MaslGuard]},
  { path: '**', redirectTo: 'home', pathMatch: 'full' }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' }) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule { }
