import { Routes } from "@angular/router";
import { ValuesAddHomeComponent } from "./values-add-home/values-add-home.component";

export const Rout_ValuesAdd:Routes = [
    {
        path: 'values-add',
        component: ValuesAddHomeComponent
    },    
    {
        path: '**',
        pathMatch: 'full',
        redirectTo: 'values-add'
    }
]
