import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MsalService } from '@azure/msal-angular';
import { AuthenticationResult } from '@azure/msal-browser';
import { OAuthService } from 'angular-oauth2-oidc';
import { JwksValidationHandler } from 'angular-oauth2-oidc-jwks';
//import { authConfig } from './soo.config';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';

  apiResponse: string;

  constructor(public authService: MsalService, private httpClient: HttpClient) {
    this.login();
    this.callProfile();
  }

  ngOnInit(): void {
    this.authService.instance.handleRedirectPromise().then(
      res => {
      if (res != null && res.account != null) {
        this.authService.instance.setActiveAccount(res.account)
      }
    })
  }

  isLoggedIn(): boolean {
    return this.authService.instance.getActiveAccount() != null
  }

  login() {
     this.authService.loginRedirect();

    //this.authService.loginPopup()
    //  .subscribe((response: AuthenticationResult) => {
    //    this.authService.instance.setActiveAccount(response.account);
    //  });
  }
  callProfile() {
    this.httpClient.get("https://graph.microsoft.com/v1.0/me").subscribe(resp => {
      this.apiResponse = JSON.stringify(resp)
    })
  }
}
