import { Routes } from "@angular/router";
import { M365SuscripcionesComponent } from "./m365-suscripciones/m365-suscripciones.component";

export const Rout_M365:Routes = [
    {
        path: 'm365',
        component: M365SuscripcionesComponent
    },    
    {
        path: '**',
        pathMatch: 'full',
        redirectTo: 'm365'
    }
]
